.container-profileadmin {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    /* background-color: rgb(182, 255, 255); */
    padding-left: 90px;
    padding-right: 90px;
    padding-top: 29px;
    overflow-y: auto;
}

.go-back-button {
    font-size: 15px;
    color: #8F8F8F;
    cursor: pointer;
    margin-left: 5px;
}

.go-back-button:hover {
    text-decoration: underline;
    color: #646464;
}

.title-profileadmin {
    width: 100%;
    height: auto;
    font-size: 18px;
    font-weight: bold;
    color: black;
    margin-bottom: 10px;
}

.description-profileadmin {
    display: flex;
    align-items: center;
    width: 100%;
    height: auto;
    font-size: 13px;
    color: #8F8F8F;
    margin-bottom: 19px;
}

.name-admin-profileadmin {
    width: auto;
    height: auto;
    font-size: 15px;
    font-weight: bold;
    margin-left: 5px;
}

.select-type-post-profileadmin {
    display: flex;
    align-items: center;
    width: 100%;
    min-height: 28px;
    font-size: 14px;
    color: #C3C3C3;
    /* background-color: cadetblue; */
}

.area-list-post-profileadmin {
    position: relative;
    display: grid;
    grid-template-columns: 300px 300px 300px 300px;
    grid-column-gap: 20px;
    grid-row-gap: 20px;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-right: 20px;
    width: 100%;
    height: 100vh;
    /* background-color: blueviolet; */
}

.filter-profileadmin {
    display: flex;
    width: 100%;
    height: 30px;
    display: flex;
    align-items: center;
    margin-bottom: 15px;
}

.table-profileadmin {
    
}

.filter-label-profileadmin {
    font-size: 16px;
    width: auto;
    height: auto;
    padding-right: 15px;
}