.container-item-manageadmin {
    display: flex;
    align-items: center;
    width: auto;
    height: 54px;
    padding: 0 10px;
    border-radius: 10px;
    margin-bottom: 16px;
    background-color: rgb(255, 255, 255);
}

.container-item-manageadmin div {
    display: flex;
    align-items: center;
}

.container-item-manageadmin div .text-overflow {
    display: block;
}

.container-item-manageadmin:last-child {
    margin-bottom: 0;
}

.profile-admin {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    width: 38px;
    min-width: 38px;
    height: 38px;
    border-radius: 50%;
}

.type-admin {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px 8px;
    width: auto;
    height: auto;
    border-radius: 6px;
    font-size: 14px;
    font-weight: bold;
    color: aliceblue;
}

.display-flex {
    display: flex;
    height: 100%;
}

