.container-navprofile {
    width: 100%;
    height: 186px;
    min-height: 186px;
    /* background-color: aqua!important; */
    background-color: rgb(255, 255, 255);
    border-bottom: 1px solid #D9D9D9;
    padding: 29px 124px;
}

.content-navprofile {
    display: flex;
    width: auto;
    min-width: 621px;
    height: 100%;
    /* background-color: rgb(255, 233, 203); */
}

.profile-navprofile {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 128px;
    min-width: 128px;
    height: 128px;
    border-radius: 50%;
    overflow: hidden;
    background-color: rgb(255, 255, 255);
    outline: 2px solid #d6d6d6;
    margin-right: 40px;
}

.contect-item-navprofile {
    display: flex;
    flex-direction: column;
    width: auto;
    height: 100%;
}

.content-top-navprofile {
    display: flex;
    align-items: flex-start;
    font-size: 27px;
    font-weight: bold;
    min-height: 32px;
    height: auto;
    width: 100%;
    margin-bottom: 5px;
    /* background-color: cadetblue; */
}

