.item-bio-navprofile div:nth-child(2) {
    width: 100%;
    height: 30px;
    /* background-color: #83ff0f; */
}

.item-bio-navprofile {
    display: flex;
    flex-direction: column;
    width: auto;
    min-width: 262px;
    height: auto;
    font-size: 16px;
    /* background-color: aqua; */
}

.content-bottom-navprofile {
    display: flex;
    height: auto;
    width: 100%;
    margin-top: 8px;
    color: #5f5f5f;
    /* background-color: rgb(118, 237, 241); */
}

